/* Sprite */
.radio, .drop li, .slct, .check {
  background: url(../img/sprite.png) 0 0 no-repeat;
}

/* Form */
.customForm {
  color: #444444;
  width: 320px;
  //margin: 2em auto;
  font: 13px/1.8 Helvetica;
}
.customForm input, .customForm textarea {
  font: 13px Helvetica;
}

/* = Input */
.customForm input {
  width: 300px;
  font-size: 13px;
  padding: 6px 0 4px 10px;
  border: 1px solid #cecece;
  background: #F6F6f6;
  border-radius: 5px;
}
.customForm textarea {
  display: block;
  /* = Убираем скролл */
  overflow: auto;

  /* = Убираем увеличение */
  resize: none;
  width: 300px;
  height: 50px;

  /* = Добавим фон, рамку, отступ */
  background: #f6f6f6;
  border: 1px solid #cecece;
  border-radius: 5px 0 0 0;
  padding: 8px 0 8px 10px;
}
/* = Переключатели Radio Button */
.radio {
  display: block;
  height: 25px;
  background-position: 0 -56px;
  coloR: #444;
  cursor: pointer;
  text-indent: 22px;
  font-size: 13px;
}
.radio.active {
  background-position: 0 -86px;
}
.radio.error {
  border-color: #ff0000;
}

/*	=	CheckBox */
/* Стилизируем чекбокс, точнее скрываем его */
.check input[type=checkbox] {
  position: absolute;
  left: -10000px;
}
.check {
  background-position: 0 3px;
  padding-left: 25px;
  cursor: pointer;
  position: relative;
}
.check.active {
  background-position: 0 -27px;
}

/*	=	Select */
.slct {
  display: block;
  border-radius: 5px;
  border: 1px solid #cecece;
  background-color: #F6F6f6;
  width: 285px;
  padding: 4px 15px 4px 10px;
  color: #444;
  background-position: 290px -145px;

  /*
      Супер финт обрезаем текст
      чтобы не вылезал за рамку
  */
  overflow: hidden;
  white-space:nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;

}
.slct.active {
  border-radius: 5px 5px 0 0;
  border-bottom: none;
}
.drop {
  //margin: 0;
  padding: 0;
  width: 310px;
  border: 1px solid #cecece;
  border-top: none;
  display: none;
  position: absolute;
  background: #fff;
}
.drop li {
  list-style: none;
  border-top: 1px dotted #e8e8e8;
  cursor: pointer;
  display: block;
  color: #444;
  padding: 4px 15px 4px 25px;
  background-position: 10px -119px;
}
.drop li:hover {
  background-color: #e8e8e8;
  color: #222;
}

/*	=	File Load */
/* Стили для контейнера */
.file-load-block {
  width: 310px;
  height: 35px;
  overflow: hidden;
  position: relative;
}
/* работаем с инпутом который конкретно file */
.file-load-block input[type=file] {
  /* делаем кроссбраузерную прозрачность */
  opacity: 0;
  filter: alpha(opacity=0);

  /* Позиционируем абсолютно отсносительно контейнера по правому краю */
  position: absolute;
  top: 0;
  right: 0;

  /*
  Задаем высоту и делаем шрифт огромным
  для того чтобы кнопка "обзор" занимала все пространство контейнера
  */
  height: 35px;
  font-size: 600px;

  /* Ставим наш инпут поверх всего содержимого */
  z-index: 2;
  cursor: pointer;
  width: auto;
}
/* Стилизируем инпут и кнопку которые под загрузчиком */
.file-load-block input[type=text] {
  z-index: 1;
  width: 190px;
  padding-right: 10px;
  white-space:nowrap;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
}
.file-load-block button {
  position: absolute;
  right: 0;
  top: 2px;
  width: 90px;
  height: 28px;
  border: 1px solid #ccc;
  background: #fff;
  color: #666;
  border-radius: 5px;
  box-shadow: inset 0 2px 2px #ccc;
}
.file-load-block .button-hover {
  box-shadow: 0 0 2px #ccc;
}

/*	=	Reset Form */
.reset-form button {
  width: 90px;
  height: 28px;
  border: 1px solid #ccc;
  background: #fff;
  color: #666;
  border-radius: 5px;
  box-shadow: inset 0 2px 2px #ccc;
  cursor: pointer;
}
.reset-form button:hover {
  box-shadow: 0 0 2px #ccc;
}
.reset-form button:active {
  margin-top: 1px;
  margin-bottom: -1px;
  zoom: 1;
}

/*	=	Form Send */
.sendmail button {
  width: 90px;
  height: 28px;
  border: 1px solid #ccc;
  background: #fff;
  color: #666;
  border-radius: 5px;
  box-shadow: inset 0 2px 2px #ccc;
  cursor: pointer;
}
.sendmail button:hover {
  box-shadow: 0 0 2px #ccc;
}
.sendmail button:active {
  margin-top: 1px;
  margin-bottom: -1px;
  zoom: 1;
}
